import React from 'react';
import Relay from 'react-relay';

class Shop extends React.Component {
  render() {
    return (
      <div>
        Store: {this.props.shop.name}
      </div>
    )
  }
}

export default Relay.createContainer(Shop, {
  fragments: {
    shop: () => Relay.QL`
      fragment on Shop {
        name
      }
    `,
  },
});



